import React from "react";
import { withRouter } from "react-router-dom";

class Profile extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
      data: {},
    };
  }
  componentDidMount(){
    let temp = localStorage.getItem("user_data");
    let data = JSON.parse(temp)
    console.log(data)
    this.setState({
        data: data
    })
  }
  render() {
    return (
    <div>
      <center>
        <h1>Profile</h1>
        <p>{this.state.data.displayName}</p>
        <img src={this.state.data.photoURL}/>
      </center>
    </div>
    
    );
  }
}
export default withRouter(Profile);