import React from "react";
import { withRouter } from "react-router-dom";
import {initializeApp} from 'firebase/app';
import {
  getAuth,
  signInWithEmailAndPassword,
  GoogleAuthProvider, signInWithPopup,
} from 'firebase/auth';
import { ToastContainer, toast } from 'react-toastify';

import 'react-toastify/dist/ReactToastify.css';
import './login.css'

const firebaseConfig = {
  apiKey: "AIzaSyD-cThGcda6hPFJm_UAIhgnCssnEXE2u9g",
  authDomain: "fswd-nit.firebaseapp.com",
  projectId: "fswd-nit",
  storageBucket: "fswd-nit.appspot.com",
  messagingSenderId: "659437110194",
  appId: "1:659437110194:web:68c92abe2e6fd081aa1aab"
};

const app = initializeApp(firebaseConfig);

class Login extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: ""
    };
  }
  navigateTo = (val) => {
    //this.props.history.push(val);
    if(val == "login"){
        this.props.history.push("/login");
    }
    if(val == "register"){
        this.props.history.push("/register");
    }
  }
  handleEmail = (x) => {
    //console.log(x.target.value.length)
    this.setState({ email: x.target.value });
  };
  handlePassword = (x) => {
    //console.log(x.target.value.length)
    this.setState({ password: x.target.value });
  };
  handleSuccessfulLogin = (x) => {
    toast.success('Account Logged In', {
      position: "bottom-center",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      theme: "colored",
      });
    console.log(x.user)
    let temp = x.user
    localStorage.setItem("user_data", JSON.stringify(temp));
    this.props.history.push("/profile")
  }
  handleLogin = () => {
    let eid = this.state.email
    var psd = this.state.password

    if(eid.length<5 || psd.length<8){
      toast.error('Incorrect Email ID / Password', {
        position: "bottom-center",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "colored",
        });
    }
    else{
      const auth = getAuth(app)
      signInWithEmailAndPassword(auth, eid, psd)
        .then((x) => this.handleSuccessfulLogin(x))
        .catch((error) => {
          console.log("Error IS ====>", error);
          toast.warn('Incorrect Credentials', {
            position: "bottom-center",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "colored",
            });
        })
    }
  }
  handleGoogleRegister = () => {
    const auth = getAuth(app);
    const googleProvider = new GoogleAuthProvider();

    signInWithPopup(auth, googleProvider)
      .then((res) => this.handleSuccessfulLogin(res))
      .catch((error) => {
        console.log("Error IS ====>", error);
      })
  }
  componentDidMount(){
  }
  render() {
    let ar = "rahul"
    return (
    <div>
      <center>
        <h1>Login</h1>
        <input 
          type="text"
          value={this.state.email}
          onChange={(x)=>this.handleEmail(x)}
          placeholder="Email ID"
          className="enterEmail"
        />
        <br/>
        <input 
          type="password"
          value={this.state.password}
          onChange={(x)=>this.handlePassword(x)}
          placeholder="Password"
          className="enterEmail"
        />
        <br/>
        <button className="sendMail" onClick={() => this.handleLogin()}>
          <p>Login</p>
        </button>
        <br/>
        <button className="googleLogin" onClick={() => this.handleGoogleRegister()}>
          <p>Sign In with Google</p>
        </button>
      </center>
      <ToastContainer
        position="bottom-center"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="colored"
      />
    </div>
    
    );
  }
}
export default withRouter(Login);